.footer {
    background-color: $primary-color;
    padding: 32px 0;

    @media screen and (min-width: $tablet) {
        padding: 0;
        min-height: 268px;
        display: flex;
        align-items: center;
    }

    @media screen and (min-width: $desktop) {
        min-height: 299px;
    }
}

.footer-container {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;

    @media screen and (min-width: $tablet) {
        gap: 0;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
}

.footer__logo {
    display: none;

    @media screen and (min-width: $desktop) {
        display: block;
    }
}

.footer__icon {
    fill: $basic-white;
}


.footer__title {
    @include font (700, 18px, calc(22/18));
    color: $basic-white;
    margin-bottom: 18px;

    @media screen and (min-width: $tablet) {
        font-size: 20px;
        line-height: calc(24/20);
    }
}

.footer-nav__list {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.footer-nav__link {
    @extend %footer-text;

    @media screen and (min-width: $tablet) {
        font-size: 16px;
        line-height: calc(19/16);
    }
}

.working {
    margin-left: auto;

    @media screen and (min-width: $tablet) {
        margin-left: 0;
    }
}

.working__text {
    @extend %footer-text;

    @media screen and (min-width: $tablet) {
        font-size: 16px;
        line-height: calc(19/16);
    }
}

.contacts__list {
    display: flex;
    gap: 12px;
    flex-direction: column;
}

.contacts__link {
    @include font (500, 13.7px, calc(17/14));
    color: $basic-white;
    font-style: normal;
    overflow-wrap: break-word;

    @media screen and (min-width: $tablet) {
        font-size: 16px;
        line-height: calc(19/16);
    }
}

.contacts__item--break {
    width: 115px;

    @media screen and (min-width: $tablet) {
        width: auto;
    }
}

.social__list {
    display: flex;
    gap: 14px;

    @media screen and (min-width: $tablet) {
        gap: 18px;
        flex-direction: column;
    }
}